<template>
    <div>
        <v-navigation-drawer
                temporary
                disable-resize-watcher
                v-model="drawer"
                :mini-variant="miniVariant"
                fixed
                app
        >
        <app-drawer
                :categories="categories"
                :signInned="signInned"
        ></app-drawer>
        </v-navigation-drawer>
        <v-app-bar
                fixed
                app
        >
            <v-app-bar-nav-icon  @click.stop="drawer = !drawer" />
            <v-spacer/>
            <app-logo></app-logo>
            <v-spacer/>
            <app-menu
                :auth="auth"
                :signInned="signInned"
            ></app-menu>
        </v-app-bar>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    import appDrawer from './appbar/Drawer'
    import appLogo from './appbar/Logo'
    import appMenu from './appbar/Menu'

    export default {
        components: {
           appDrawer,
            appLogo,
            appMenu
        },
        computed: {
          ...mapGetters([
              'signInned',
              'auth'
          ])
        },
        data: () => ({
            clipped: false,
            drawer: false,
            fixed: false,
            miniVariant: false,
            categories: []
        }),
        methods: {
            ...mapActions([
                'filterCategory'
            ]),
          async fetchCategories () {
             const results = await this.$axios.get('category/getall')
              this.categories = results.data.categories
          },
            filterCate (id) {
                this.$store.commit('SetCATEGORY', id)
                this.filterCategory(1)
                this.drawer = false
            }
        },
        mounted () {
                this.$io.on('message', (msg) => {
                    if(msg.to === this.auth._id) {
                        this.$swal({
                            title: msg.message,
                            toast: true,
                            position: 'top-end',
                            showConfirmButton: false,
                            timer: 3000
                        })
                    }
                })
        },
    }
</script>
